.figure {
	&-caption {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		color: #fff;
		height: 60%;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		transition: height 0.25s linear;
		z-index: 1;

		&__title {
			font-weight: 700;
			@include font-size(24px);
			transition: all 0.25s linear;

			@include media-breakpoint-down(md) {
				@include font-size(20px);
			}
		}

		&:after {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			height: 100%;
			content: '';
			z-index: -1;
			opacity: 0.5;
			transition: opacity 0.25s linear;
			background: -moz-linear-gradient(top, rgba(37, 53, 73, 0) 0%, rgba(37, 53, 73, 1) 100%);
			background: -webkit-linear-gradient(top, rgba(37, 53, 73, 0) 0%, rgba(37, 53, 73, 1) 100%);
			background: linear-gradient(to bottom, rgba(37, 53, 73, 0) 0%, rgba(37, 53, 73, 1) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00253549', endColorstr='#253549', GradientType=0);
		}
	}

	&-img {
		&_stretch {
			height: 100%;
			width: 100%;
			object-fit: cover;
		}
	}

	&__btn {
		z-index: -1;
		opacity: 0;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: calc(70% - 15px);
		left: 0;
		right: 0;
		margin: auto;
		transition: all 0.25s linear;

		@include media-breakpoint-down(md) {
			width: calc(100% - 15px);
			@include font-size(16px);
		}
	}

	@include hover-focus {
		.figure-caption {
			height: 100%;

			&:after {
				opacity: 1;
			}

			&__title {
				opacity: 0;
				z-index: -1;
			}
		}

		.figure__btn {
			z-index: 2;
			opacity: 1;
		}
	}
}

.fancybox-caption__body {
	@include media-breakpoint-up(lg) {
		width: 50%;
		margin: 0 auto;
	}
}
