.footer {
	background-color: #0d1b2b;
	color: #fff;

	&__ {
		&title {
			@include font-size(45px);
			text-transform: uppercase;
			letter-spacing: -1px;
			font-weight: bold;
		}

		&link {
			display: block;
			margin: 0 0 1rem;

			&-label {
				color: $link-color;
			}
		}

		&phone {
			@include font-size(30px);
			display: block;
			line-height: 1.4;
			letter-spacing: -0.5px;
		}

		&container {
			max-width: 1260px;
		}

		&address {
			max-width: 371px;

			&-links {
				@include media-breakpoint-up(xl) {
					padding-left: 2.2rem;
				}
			}
		}

		&form {
			border: 5px solid #546682;
			max-width: 555px;
			padding: 40px 40px 30px;
			font-family: $font-family-DINPro, Arial, Helvetica, sans-serif;

			&-btn {
				width: 290px;
			}

			&-link {
				font-size: 14px;
				font-weight: 500;
				color: #4c89c1;
				text-decoration: underline;

				@include hover-focus {
					text-decoration: none;
				}
			}

			.form-control {
				&:focus {
					box-shadow: 0 0 0 0.2rem #f8f9fa;
				}
			}

			@include media-breakpoint-down(md) {
				padding: 30px;
			}

			@include media-breakpoint-down(sm) {
				padding: 25px 15px;
				border-width: 3px;
				max-width: 100%;
			}
		}
	}

	textarea.footer__textarea {
		height: 112px;
	}

	.col {
		@include media-breakpoint-up(lg) {
			&_ {
				&border-right {
					border-right: 3px solid #0058a7;
				}
			}
		}
	}
}
