// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;
$body-color: #08172d;
$gray-600: #00264c;
$link-color: #3d95e3;

// FONTS

$font-size-base: 18;
$font-family-Myriad: 'Myriad Pro';
$font-family-DINPro: 'DINPro';
$font-family-base: $font-family-Myriad, Arial, sans-serif;
$enable-responsive-font-sizes: true;

// EASING TRANSITIONS

$duration-normal: 0.4s;
$easing-back-out: cubic-bezier(0.25, 0.74, 0.22, 1.1); // http://cubic-bezier.com/#.25,.74,.22,1.1
$easing-back-in: cubic-bezier(0.47, -0.1, 0.5, -0.06); // http://cubic-bezier.com/#.47,-0.1,.5,-.06
$easing-out: ease-out;
$easing-in: ease-in;

// HEADINGS

$headings-font-family: $font-family-DINPro, Arial, sans-serif;
$headings-font-weight: 400;
$h1-font-size: 46px;
$headings-line-height: 1.1;

// CONTAINER - GRID

$container-max-widths: (sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1200px) !default;

// BUTTONS

$btn-border-width: 3px;
$btn-font-weight: 700;
$btn-font-family: $font-family-DINPro, Arial, sans-serif;
$btn-padding-y: 0.85rem;
$btn-padding-x: 1.5rem;

// INPUTS, FORMS
$input-bg: #546682;
$input-color: #fff;
$input-border-width: 0;
$input-placeholder-color: #fff;
$input-font-weight: 500;
$input-font-family: $font-family-DINPro, Arial, sans-serif;
$input-font-size: 15;
$input-height: 55px;
$input-padding-x: 2.2rem;
$input-padding-y: 1rem;
$form-group-margin-bottom: 3px;
